.nav__outer-wrapper {
  display: flex;
  justify-content: space-between;
  padding: 15px 20px;
  align-items: center;
}

.nav__fullpage-outer-wrapper {
  position: fixed;
  width: 100vw;
  height: 100vh;
  z-index: 99999;
  background: #1d1e22;
  display: flex;
  padding: 35px;
  color: #fff;
  overflow: hidden;
}

.nav__bg {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.nav__fullpage-inner-wrapper {
  align-self: flex-end;
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 2;
}

.nav__link {
  color: #fff;
  font-weight: 500;
  font-size: 1.8em;
  margin-top: 1.5em;
  font-family: "Moderat Regular", sans-serif !important;
}

.active {
  color: #2bb786;
}

.nav__inner-wrapper {
  align-self: flex-end;
}

.nav__menu {
  z-index: 999999;
}

.nav__icon {
  max-width: 20px;
  mix-blend-mode: difference;
}

.nav__brand-link {
  color: #000;
  font-family: "Moderat Extended", sans-serif;
  font-size: 1.1em;
}

.nav__brand {
  font-family: "Moderat Medium", sans-serif;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.nav__logo {
  width: 15px;
  margin-right: 7px;
}

.nav__spacer {
  height: 15px;
}
